// Collapsible 컴포넌트
const Collapsible = () => {
  const [activeIndex, setActiveIndex] = useState(null);

  const handleClick = (index) => {
    // 현재 클릭한 버튼이 이미 열려 있으면 닫고, 그렇지 않으면 열기
    setActiveIndex(activeIndex === index ? null : index);
  };

  return (
    <div>
      <button
        type="button"
        className={`collapsible ${activeIndex === 0 ? 'active' : ''}`}
        onClick={() => handleClick(0)}
      >
        알아보기
      </button>
      <div
        className="content"
        style={{
          maxHeight: activeIndex === 0 ? '500px' : '0',
          padding: activeIndex === 0 ? '0' : '0',
        }}
      >
          <ul>
            <li><a href="#2Page">소개&nbsp;:&nbsp;<span>ABOUT</span></a></li>
            <li><a href="#3Page">작업물&nbsp;:&nbsp;<span>PORTFOLIO</span></a></li>
            <li><a href="#4Page">디자인&nbsp;:&nbsp;<span>DESIGN</span></a></li>
            <li><a href="#5Page">연락처&nbsp;:&nbsp;<span>CONTACT</span></a></li>

          </ul>
          
              <button type="button"
                className={`collapsible ${activeIndex === 0 ? 'active' : ''}`}
                onClick={() => handleClick(0)}>
                  닫기 X
              </button>
            
      </div>
    </div>
  );
};

