import React from 'react';
import { Container, Row, Col, Image } from 'react-bootstrap';
import profileImage from '../images/profile.jpg';
import './About.css'; // CSS 파일 임포트

const About = () => {
  return (
    <Container id="about" className="my-5">
        <Row>
        <Col md={6}>
          <h2>About Me</h2>
          <p>
            저는 UX/UI 디자이너로서 다양한 프로젝트에서 경험을 쌓아왔습니다. 사용자의 경험을 최우선으로
            생각하며, 디자인에 대한 깊은 이해와 창의적인 접근으로 문제를 해결합니다.
          </p>
        </Col>
        <Col md={6}>
          <Image 
            src={profileImage} 
            alt="Profile"
            width="350px" 
            className="profile-image" // CSS 클래스를 추가하여 스타일 적용
          />
        </Col>
      </Row>
    </Container>
  );
};

export default About;
